.gc .gc-fix-stacking-context {
  z-index: 6402;
  position: relative; }

.gc .gc-relative {
  position: relative; }

.gc .gc-fixed {
  position: fixed; }

.gc .gc-highlighted {
  z-index: 6402; }
  .gc .gc-highlighted > td, .gc .gc-highlighted > th {
    position: relative;
    z-index: 6402; }

.gc-overlay {
  position: fixed;
  z-index: 6400;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  box-sizing: content-box;
  transition: all 0.3s ease-out;
  opacity: 0.8;
  background-color: #000; }

.gc-highlight {
  position: absolute;
  z-index: 6401;
  box-sizing: content-box;
  -moz-transition: all 0.3s ease-out;
  -o-transition: all 0.3s ease-out;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
  border-radius: 4px;
  background-color: rgba(255, 255, 255, 0.9);
  box-shadow: 0 2px 15px rgba(0, 0, 0, 0.4); }
  .gc-highlight:before, .gc-highlight:after {
    -moz-box-sizing: content-box;
    -webkit-box-sizing: content-box;
    box-sizing: content-box; }

.gc-control {
  position: absolute;
  z-index: 6404;
  visibility: hidden;
  box-sizing: content-box;
  -moz-transition: all 0.3s ease-out;
  -o-transition: all 0.3s ease-out;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
  background-color: transparent; }

.gc-interaction {
  position: absolute; }
  .gc-interaction.gc-disable {
    position: absolute;
    z-index: 6403;
    opacity: 0;
    background-color: white;
    filter: alpha(opacity=0); }

.gc-tooltip {
  position: absolute;
  visibility: visible;
  box-sizing: content-box;
  min-width: 300px;
  max-width: 550px;
  padding: 25px;
  -moz-transition: opacity 0.1s ease-out;
  -o-transition: opacity 0.1s ease-out;
  -webkit-transition: opacity 0.1s ease-out;
  transition: opacity 0.1s ease-out;
  border-radius: 3px;
  background-color: white;
  box-shadow: 0 1px 10px rgba(0, 0, 0, 0.4); }
  .gc-tooltip[data-guidechimp-position='top'] {
    margin-bottom: 10px; }
  .gc-tooltip[data-guidechimp-position='left'] {
    margin: 0 10px 0 10px; }
  .gc-tooltip[data-guidechimp-position='right'] {
    margin: 0 10px 0 10px; }
  .gc-tooltip[data-guidechimp-position='bottom'] {
    margin-top: 10px; }
  .gc-tooltip[data-guidechimp-alignment='right'] {
    margin-left: 5px; }
  .gc-tooltip[data-guidechimp-alignment='left'] {
    margin-right: 5px; }

.gc-tooltip-tail {
  position: absolute;
  content: '';
  border: 5px solid transparent; }
  .gc-tooltip[data-guidechimp-position='top'] .gc-tooltip-tail {
    bottom: -10px;
    border-top-color: white; }
  .gc-tooltip[data-guidechimp-position='top'][data-guidechimp-alignment='right'] .gc-tooltip-tail {
    right: 10px;
    bottom: -10px;
    border-top-color: white; }
  .gc-tooltip[data-guidechimp-position='top'][data-guidechimp-alignment='middle'] .gc-tooltip-tail {
    bottom: -10px;
    left: 50%;
    margin-left: -5px;
    border-top-color: white; }
  .gc-tooltip[data-guidechimp-position='left'] .gc-tooltip-tail {
    top: 10px;
    right: -10px;
    border-left-color: white; }
  .gc-tooltip[data-guidechimp-position='right'] .gc-tooltip-tail {
    top: 10px;
    left: -10px;
    border-right-color: white; }
  .gc-tooltip[data-guidechimp-position='bottom'] .gc-tooltip-tail {
    top: -10px;
    border-bottom-color: white; }
  .gc-tooltip[data-guidechimp-position='bottom'][data-guidechimp-alignment='right'] .gc-tooltip-tail {
    top: -10px;
    right: 10px;
    border-bottom-color: white; }
  .gc-tooltip[data-guidechimp-position='bottom'][data-guidechimp-alignment='middle'] .gc-tooltip-tail {
    top: -10px;
    left: 50%;
    margin-left: -5px;
    border-bottom-color: white; }

.gc-close {
  position: absolute;
  top: 0;
  right: 0;
  display: inline-block;
  overflow: hidden;
  width: 15px;
  height: 15px;
  margin: 10px;
  cursor: pointer;
  opacity: 0.4; }
  .gc-close:hover {
    opacity: 1; }
  .gc-close::before, .gc-close::after {
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    height: 2px;
    margin-top: -1px;
    content: '';
    border-radius: 5px;
    background: #000; }
  .gc-close::before {
    transform: rotate(45deg); }
  .gc-close::after {
    transform: rotate(-45deg); }

.gc-progressbar {
  font-size: 10px;
  line-height: 10px;
  position: absolute;
  top: 0;
  left: 0;
  float: left;
  box-sizing: content-box;
  width: 0;
  height: 5px;
  text-align: center;
  border-radius: 2px 0 0 0;
  background-color: #e14817; }
  .gc-progressbar[aria-valuenow='100'] {
    border-radius: 2px 2px 0 0; }
  .gc-tooltip[data-guidechimp-position='bottom'] .gc-progressbar {
    top: auto;
    bottom: 0;
    border-radius: 0 0 0 2px; }
    .gc-tooltip[data-guidechimp-position='bottom'] .gc-progressbar[aria-valuenow='100'] {
      border-radius: 0 0 2px 2px; }

.gc-default {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0; }

.gc-title {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 20px;
  padding: 0 5px 0 5px; }
  .gc-title.gc-hidden {
    display: none; }

.gc-description {
  font-size: 14px;
  overflow-y: auto;
  max-height: 200px;
  margin-bottom: 20px;
  padding: 0 5px 0 5px;
  text-align: justify;
  scroll-behavior: smooth; }
  .gc-description.gc-hidden {
    display: none; }

.gc-custom-buttons {
  margin-bottom: 20px; }
  .gc-custom-buttons.gc-hidden {
    display: none; }

.gc-navigation {
  display: flex;
  justify-content: space-between;
  padding: 20px 10px 10px 10px; }
  .gc-navigation.gc-hidden {
    display: none; }

.gc-navigation-prev,
.gc-navigation-next {
  width: 15px;
  height: 15px;
  cursor: pointer;
  border-right: 5px solid #bebebe;
  border-bottom: 5px solid #bebebe; }
  .gc-navigation-prev:hover,
  .gc-navigation-next:hover {
    opacity: 1;
    border-color: #e14817; }
  .gc-navigation-prev.gc-hidden,
  .gc-navigation-next.gc-hidden {
    visibility: hidden; }

.gc-navigation-prev {
  transform: rotate(-225deg); }

.gc-navigation-next {
  transform: rotate(-45deg); }

.gc-pagination.gc-hidden {
  visibility: hidden; }

.gc-pagination-item {
  display: inline-block;
  box-sizing: content-box;
  width: 8px;
  height: 8px;
  padding: 0;
  cursor: pointer;
  border: 8px solid #ffffff;
  border-radius: 50%;
  background-color: #bebebe; }
  .gc-pagination-item:hover {
    background-color: #e14817; }

.gc-pagination-active {
  background-color: #e14817; }

.gc-copyright {
  font-family: Arial, serif;
  font-size: 10px;
  position: absolute;
  bottom: 0;
  left: 0;
  margin: 0 0 5px 10px;
  letter-spacing: 0.1em;
  color: #888888; }

.gc-preloader {
  position: fixed;
  z-index: 6405;
  top: 50%;
  left: 50%;
  width: 80px;
  height: 80px;
  transform: translate(-50%, -50%); }
  .gc-preloader:after {
    display: block;
    width: 64px;
    height: 64px;
    margin: 8px;
    content: " ";
    animation: gc-preloader 1.2s linear infinite;
    border: 6px solid #fff;
    border-color: #fff transparent #fff transparent;
    border-radius: 50%; }

@keyframes gc-preloader {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

@media (max-width: 360px) {
  .gc-tooltip {
    min-width: 260px;
    max-width: 290px; } }
